import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'

Vue.use(VueRouter)

  const routes = [
  {
      path: '/',
      name: 'Login',
      component: Login,
      hidden:true
  },{
      path: '/home',
      name: 'Home',
      component: ()=>import("../views/Home.vue"),
      hidden:true
  }
]

const router = new VueRouter({
  routes
})

export default router
